.hacker-news-feed {
  white-space: nowrap;
  overflow-x: auto; }
  .hacker-news-feed table {
    table-layout: fixed;
    border-collapse: collapse;
    border-spacing: 0; }
    .hacker-news-feed table tbody tr:nth-child(even) {
      background-color: #dadada; }
    .hacker-news-feed table th,
    .hacker-news-feed table td {
      white-space: nowrap;
      padding: 4px; }
    .hacker-news-feed table th {
      font-size: 12px;
      line-height: 12px/16px; }
    .hacker-news-feed table td.url, .hacker-news-feed table td.time {
      color: #707070;
      font-size: 13px;
      max-width: 200px;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden; }
      .hacker-news-feed table td.url a, .hacker-news-feed table td.time a {
        display: inline; }
    .hacker-news-feed table td .upvote-control button {
      width: 100%;
      padding: 12px 16px; }

.recharts-responsive-container {
  margin-top: 24px; }
